.toggle {
	position: absolute;
	top: 50%;
	right: 10px;
	width: 36px;
	height: 36px;
	transform: translate(-50%, -50%);
	transition: background-color .3s;
	cursor: pointer;
	z-index: 20;
	@include md {
		width: 24px;
		height: 24px; }
	&__line {
		position: absolute;
		display: block;
		width: 50%;
		height: 4px;
		background-color: $color-cerulean;
		transition: .5s; }
	&.is-active {
		.toggle__line {
			background-color: $color-cerulean; } }
	&__line:nth-child(even) {
		left: 50%; }
	&__line:nth-child(1),
	&__line:nth-child(2) {
		top: 0 {
        border-radius: 3px; } }
	&__line:nth-child(3),
	&__line:nth-child(4) {
		top: 12px;
		@include md {
			top: 8px; } }
	&__line:nth-child(5),
	&__line:nth-child(6) {
		top: 24px;
		@include md {
			top: 16px; } } }
.toggle.is-active .toggle__line:nth-child(3) {
	transform: translateX(-50px);
	opacity: 0; }

.toggle.is-active .toggle__line:nth-child(4) {
	transform: translateX(50px);
	opacity: 0; }

.toggle .toggle__line:nth-child(1) {
	transform-origin: top left; }

.toggle .toggle__line:nth-child(2) {
	transform-origin: top right; }

.toggle.is-active .toggle__line:nth-child(1) {
	transform: rotate(45deg);
	top: -3px;
	left: 7px;
	@include md {
		left: 4px; } }

.toggle.is-active .toggle__line:nth-child(2) {
	transform: rotate(-45deg);
	top: -3px;
	left: calc(50% - 3px); }

.toggle .toggle__line:nth-child(5) {
	transform-origin: bottom left; }

.toggle .toggle__line:nth-child(6) {
	transform-origin: bottom right; }

.toggle.is-active .toggle__line:nth-child(5) {
	transform: rotate(-45deg);
	bottom: -3px;
	left: 7px;
	@include md {
		left: 4px; } }

.toggle.is-active .toggle__line:nth-child(6) {
	transform: rotate(45deg);
	bottom: -3px;
	left: calc(50% - 3px); }

.btn {
    position: relative;
    background: none;
    font-weight: 500;
    color: $color-mine-shaft;
    z-index: 2;
    &::before {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 28px;
        height: 28px;
        content: '';
        border-radius: 50%;
        background-color: $color-cerulean;
        z-index: -1;
        opacity: 0;
        transition: opacity .3s; }
    &.is-active {
        color: $color-white;
        &::before {
            opacity: 1; } } }

