.btn-card {
	display: block;
	position: relative;
	max-width: 180px;
	width: 100%;
	height: 42px;
	margin: 0 auto;
	font-size: 0.875em;
	font-weight: 500;
	font-family: $montserrat;
	line-height: 42px;
	color: $color-white;
	text-decoration: none;
	text-align: center;
	transition: color .5s;
	overflow: hidden;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $color-cerulean;
		z-index: -1;
		transition: .5s;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; }
	&::after {
		content: '';
		position: absolute;
		top: calc(-100% + 2px);
		left: 100%;
		width: 100%;
		height: 100%;
		background-color: $color-cerulean;
		z-index: -1;
		transition: .5s;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box; }
	span {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-left: 2px solid $color-cerulean;
		border-right: 2px solid $color-cerulean;
		box-sizing: border-box;
		transform-origin: top;
		transform: scaleY(0);
		transition: .5s; }
	&:hover {
		color: $color-cerulean;
		&::before {
			top: calc( 100% - 2px); }
		&::after {
			left: 0;
			transition-delay: .5s; }
		span {
			transform: scaleY(1);
			transition-delay: 1s; } } }

.btn-up {
	position: fixed;
	right: 20px;
	bottom: 60px;
	width: 60px;
	height: 60px;
	outline: none;
	border: none;
	border-radius: 50%;
	font-size: 0;
	line-height: 10px;
	vertical-align: middle;
	text-align: center;
	background: none;
	background-color: $color-gray;
	transition: background-color .3s;
	z-index: 3;
	&:hover {
		background-color: $color-cerulean; }
	.icon {
		display: inline-block;
		width: 30px;
		height: 18px;
		fill: $color-white; }
	@include sm {
		width: 32px;
		height: 32px;
		bottom: 45px;
		right: 15px;
		line-height: 5px;
		.icon {
			width: 15px;
			height: 9px; } } }
